  <template>
  <div id="configFailed">
    <head-top title="Failed To Add Device"></head-top>
    <div class="content">
      <img src="../../../assets/addevice/failure@3x.png" alt="">
      <div class="text">
        <p>1.Confirm that the connected WiFi network is 2.4G, and enter the correct password.（No support for WIFI in the 5G band）</p>
        <p>2.Ensure that the connected WiFi network signal is stability and  smooth.</p>
        <p>3.Ensure that your device is in the state of being connected;</p>
        <p>4.Ensure that the router has closed black and white list function (ie, MAC address filtering); </p>
        <p>5.Please try to make your router, mobile phones and devices close to each other;</p>
      </div>
      <div class="retry" @click="$router.push({path: '/Me/configwifi'})">
        Retry
      </div>
    </div>
  </div>
</template>
<script>
import headTop from '../../../components/common/head'
export default {
  data () {
    return {
    }
  },
  components: {
    headTop
  }
}
</script>
<style lang="less" scoped>
@import '../../../styles/mixin.less';
#configFailed{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .content{
    display: flex;
    padding: 0 30px;
    flex-direction: column;
    .wh(100%, auto);
    img{
      .wh(207px, 280px);
      align-self: center;
      margin-top: 50px;
    }
    .text{
      margin-top: 75px;
      font-size: 28px;
      color: #9dabac;
      line-height: 40px;
      padding: 0 30px;
      .wh(100%, auto);
      text-align: left;
    }
    .retry{
      .wh-l(520px, 100px);
      color: white;
      font-size: 46px;
      margin: 0 auto;
      border-radius: 10px;
      margin-top: 74px;
      background: #13af68;
    }
  }
}
</style>



